* {
  box-sizing: border-box;
  font-family: 'VT323', monospace;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}
.card {
 transition: all 0.5s ease;
    background: rgb(85, 11, 11);
    border-radius: 2px;
    height: 250px;
    margin: 1rem;
    position: relative;
    width: 320px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
    
    
  }

  .card:hover {
    -webkit-transform: rotate(-10deg);
            transform: rotate(-5deg);
  }
  
  .card > .img-container {
    height: 90%;
    overflow: hidden;
    text-align: center;
    background:rgb(0, 0, 0);
  }
  .card > .img-container > img {
    width: 120%
  }
  
  .card > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
  }

.wrapper {
    padding-top: 50px;
    background: #ECEFF1;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    padding: 50px;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    overflow: auto;
  }
.header {
    width: 100%;
    font-size: 2rem;
    color: rgb(172, 37, 37);
    margin-bottom: 10px;
  }
  
  .title {
    float: left;
  }
  
  .scores {
    float: right;
  }
